





























import {Component} from 'vue-property-decorator';
import Slide from "@/js/views/home/PageHeader/Slide.vue";
import SliderNav from "@/js/components/SliderNav.vue";
import SliderOnMainPage, {Slide as TSlide} from "@/js/api/sliderOnMainPage";
import bus from "@/js/common/bus";
import {scrollTo} from "@/js/mixins/common";
import {mixins} from "vue-class-component";

@Component({
    components: {SliderNav, Slide},
    data: () => ({
    }),
    metaInfo: {
        title: '  ',
        meta: [
            {
                vmid: 'description',
                name: 'description',
                content: "   ",
            },
            {
                vmid: 'keywords',
                name: 'keywords',
                content: ', , ,  ,  , ,  ',
            }
        ]
    }
})

export default class PageHeader extends mixins(scrollTo) {
    currentSlideIndex = -1;
    slides: Array<TSlide> = [];

    idInterval = 0;
    mounted():void {
      SliderOnMainPage.get().then(({data}) => {
        this.slides = data
      })
        this.$nextTick(() => {
            this.initSlider()
        })
    }

    prevHandler():void {
        clearInterval(this.idInterval);
        this.setInterval();
        if (this.currentSlideIndex === 0)
            this.currentSlideIndex = this.slides.length - 1;
        else
            this.currentSlideIndex--
    }

    nextHandler():void {
        clearInterval(this.idInterval);
        this.setInterval();
        if (this.currentSlideIndex === this.slides.length - 1)
            this.currentSlideIndex = 0;
        else
            this.currentSlideIndex++
    }

    setInterval():void {
        this.idInterval = setInterval(() => {
            if (this.currentSlideIndex && this.currentSlideIndex + 1 === this.slides.length) {
                this.currentSlideIndex = 0
            } else {
                this.currentSlideIndex++
            }
        }, 5000)
    }

    initSlider():void {
        setTimeout(() => {
            this.currentSlideIndex = 0;
            this.setInterval();
        }, 500)
    }

    created():void {
        bus.$on('scroll-to-info', () => {
            this.scrollTo(0)
        })
    }

    beforeDestroy(): void {
        bus.$off('scroll-to-info')
    }
}
